.header-address {
    font-size: 16px;
    line-height: 19px;
    text-align: right;
    color: $white;
    display: none;

    &::after {
        content: "";
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $white;
        margin: 8px 17px 8px 11px;
    }

    [class*="about-page"] & {
        color: $gray-link;

        &::after {
            background-color: $gray-link;
        }
    }
}

@media (min-width: 768px) {
    .header-address {
        display: flex;
    }
}
